
import { defineComponent } from 'vue'
import { useI18n } from 'vue-i18n';
import enableTabLinksInContent from '@/util/enableTabLinksInContent';
import ModalDialog from 'brdgm-commons/src/components/structure/ModalDialog.vue'
import Expansion from '@/services/enum/Expansion';
import { useStore } from '@/store';

export default defineComponent({
  name: 'BuildRoadUpgradeLinkModal',  
  components: {
    ModalDialog
  },
  setup() {
    const { t } = useI18n()
    useStore()
    return { t }
  },
  computed: {
    hasTrafficExpansion() : boolean {
      return this.$store.state.setup.expansions.includes(Expansion.TRAFFIC)
    },
  },
  mounted() {
    enableTabLinksInContent(this.$el as HTMLElement)
  },
})
